import React from 'react';
import './index.css';
import registerServiceWorker from './registerServiceWorker';
import {render} from 'react-dom'
import {Route, HashRouter, Switch} from 'react-router-dom'
import {EditGamanec, CreateResult, GamanecPage, PaymentForm, PaymentResult, StartCompany} from "./components/PPayment";
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import {Home} from "./components/Home";

render((
    <MuiThemeProvider>
        <AppBar
            title="Публічний Гаманець"
            iconElementRight={<a href='/'>Home</a>}
        />
        <div className='page'>
            <HashRouter>
                <Switch>
                    <Route exact path='/' component={Home} />
                    <Route path='/new' component={StartCompany}/>
                    <Route path='/edit' component={EditGamanec}/>
                    <Route path='/result' component={CreateResult}/>
                    <Route path='/gamanec' component={GamanecPage}/>
                    <Route path='/payment' component={PaymentForm}/>
                    <Route path='/payment_result/:id' component={PaymentResult}/>
                </Switch>
            </HashRouter>
        </div>
    </MuiThemeProvider>
),
    document.getElementById('root'));
registerServiceWorker();




